@import "../../../utils/variables";
@import "../../../utils/mixins";

.button {
  margin: 0 2px !important;
  padding: 2px !important;
  border-radius: 10% !important;
  font-size: 12px !important;
}

.modal {
  padding: 10px;
  width: 100%;
  font-family: 'Montserrat', sans-serif;

  .header {
    text-align: center;
    overflow-wrap: break-word;
  }

  .text {
    padding: 25px 0;
  }

  .input {
    width: 100%;

    > div {
      input {
        width: 100%;
      }
    }
  }

  .textArea {
    width: 100%;

    > div {
      textarea {
        width: 100%;
        height: 120px;
      }
    }
  }

  .checkbox {
    display: flex;
    justify-content: center;

    > div {
      > input {
      }

      > label {
        padding-top: 1px;
        font-size: 16px;
      }
    }
  }

  .buttons {
    width: 100%;
    @include flexCenter;

    button {
      width: 30%;
    }
  }
}